.join-page {
    $root: &;

    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-color: #ffffff;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    background-image: url("../img/form-bg-desktop@1x.png");

    @media (max-width: 768px) {
        background-image: url("../img/form-bg-tablet@1x.png");
    }

    @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
        background-image: url("../img/form-bg-desktop@2x.png");

        @media (max-width: 768px) {
            background-image: url("../img/form-bg-tablet@2x.png") !important;
        }
    }

    &__logo-link {
        position: absolute;
        z-index: 4;
        top: 59px;
        left: 7vw;
        transition: opacity $transition-duration;

        @media (max-width: 768px) {
            top: 54px;
            left: 11vw;
        }

        &:hover {
            opacity: 0.8;
        }

        &:active {
            opacity: 0.3;
        }
    }

    &__logo-image {
        height: auto;

        @media (max-width: 768px) {
            width: 100px;
        }
    }

    &__form {
        position: relative;
        z-index: 2;

        &--hidden {
            display: none;
        }
    }

    &__inputs-container {
        position: relative;
        top: 49vh;
        left: 14vw;

        @media (max-width: 768px) {
            left: 12.5vw;
        }

        @media (max-width: 768px) {
            top: 47vh;
        }
    }

    &__input-wrapper {
        position: relative;
        max-width: 50vw;
        padding-top: 86px;
        font-size: 50px;
        line-height: 1em;
        text-transform: uppercase;
        letter-spacing: 0.04em;
        transition-property: opacity, transform;
        transition-duration: $transition-duration, 1s;

        @media (max-width: 1200px) {
            max-width: 70vw;
        }

        @media (max-width: 768px) {
            padding-top: 80px;
            font-size: 36px;
        }

        @media (max-width: 540px) {
            padding-top: 43px;
            font-size: 20px;
        }

        &--hidden {
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            transform: translateX(40%);
            transition: none;
            opacity: 0;
        }

        &--with-error {
            &::after {
                content: "Нужно ввести текст";
                position: absolute;
                bottom: -24px;
                left: 0;
                font-size: 14px;
                line-height: 1.2em;

                @media (max-width: 768px) {
                    bottom: -14px;
                    font-size: 8px;
                }
            }

            #{$root}__field {
                animation: shake-input 0.4s linear;
            }
        }
    }

    &__label {
        display: block;
        position: absolute;
        bottom: 98px;
        left: 0;
        font-weight: 700;

        @media (max-width: 768px) {
            bottom: 92px;
        }

        @media (max-width: 540px) {
            bottom: 55px;
        }

        &--with-long-text {
            font-size: 40px;

            @media (max-width: 768px) {
                font-size: 24px;
            }

            @media (max-width: 540px) {
                font-size: 16px;
            }
        }
    }

    &__field {
        border: none;
        padding: 0;
        border-bottom: 1px solid #000000;
        font: inherit;
        background-color: transparent;

        @media (max-width: 540px) {
            width: 204px;
        }

        &:focus {
            outline: none;
        }
    }

    &__next-button {
        position: relative;
        left: 180px;
        width: 200px;
        height: 180px;
        border: none;
        padding: 0;
        cursor: pointer;
        margin-top: 11vh;
        background-color: transparent;

        @media (max-width: 768px) {
            left: 80px;
            width: 180px;
            height: 140px;
            margin-top: 8vh;
        }

        @media (max-width: 540px) {
            width: 110px;
            height: 90px;
            margin-top: 11vh;
        }

        &:focus {
            outline: none;
        }

        &::before {
            content: "";
            position: absolute;
            z-index: 3;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-repeat: no-repeat;
            background-size: 40px auto;
            background-position: center center;
            background-image: url("../img/arrow-icon@1x.png");

            @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
                background-image: url("../img/arrow-icon@2x.png");
            }

            @media (max-width: 540px) {
                background-size: 28px auto;
            }
        }
    }

    &__button-wave-face {
        position: relative;
        z-index: 2;

        // path {
        //   animation: shake-button 1s infinite;
        // }
    }

    &__button-wave-shadow {
        position: absolute;
        opacity: 0.54;
        top: 24px;
        left: 8px;
    }

    &__thanks-container {
        position: relative;
        top: 36vh;
        opacity: 0;
        transition: opacity 1s;
        text-align: center;

        &--visible {
            z-index: 2;
            opacity: 1;
        }
    }

    &__thanks-text {
        display: grid;
        margin: 0;
    }

    &__thanks-top-word {
        margin-bottom: 28px;
        font-size: 64px;
        line-height: 1em;
        text-transform: uppercase;
        font-weight: 700;
        letter-spacing: 0.1em;

        @media (max-width: 768px) {
            margin-bottom: 12px;
            font-size: 48px;
        }

        @media (max-width: 540px) {
            font-size: 26px;
        }
    }

    &__thanks-bottom-words {
        max-width: 700px;
        margin: 0 auto;
        font-size: 38px;
        line-height: 1.2em;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.04em;

        @media (max-width: 768px) {
            font-size: 20px;
            max-width: 340px;
        }

        @media (max-width: 540px) {
            max-width: 260px;
            font-size: 16px;
        }
    }

    &__social-wrapper {
        margin-top: 11vh;
        display: grid;
        justify-items: center;

        @media (max-width: 768px) {
            margin-top: 6vh;
        }
    }

    &__social-text {
        max-width: 480px;
        margin: 0;
        font-size: 28px;
        line-height: 1.3em;
        font-weight: 600;
        text-transform: uppercase;

        @media (max-width: 768px) {
            max-width: 220px;
            font-size: 13px;
        }
    }

    &__social-list {
        display: flex;
        margin: 0;
        padding: 0;
        margin-top: 4vh;
        list-style: none;
    }

    &__social-item {
        margin-right: 70px;

        @media (max-width: 768px) {
            margin-right: 18px;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    &__social-icon {
        height: auto;

        @media (max-width: 768px) {
            width: 80px;
        }
    }

    &__back-link {
        display: none;
        align-items: center;
        justify-content: center;
        margin-top: 24px;
        width: 100px;
        height: 60px;
        font-size: 8px;
        line-height: 1em;
        font-weight: 600;
        letter-spacing: 0.025em;
        text-transform: uppercase;
        color: #ffffff;
        text-decoration: none;
        background-size: contain;
        background-image: url("../img/back-button-bg.svg");

        @media (max-width: 767px) {
            display: flex;
        }
    }

    &__confetti-wrapper {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &--thanks-step {
        &::after {
            opacity: 1;
        }
    }
    $root: &;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #ffffff;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 0 0;
        background-image: url("../img/thanks-bg-desktop@1x.png");
        opacity: 0;
        transition: opacity 1s;

        @media (max-width: 768px) {
            background-image: url("../img/thanks-bg-tablet@1x.png");
        }

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url("../img/thanks-bg-desktop@2x.png");

            @media (max-width: 768px) {
                background-image: url("../img/thanks-bg-tablet@2x.png") !important;
            }
        }
    }
}

html.webp .join-form {
    background-image: url("../img/form-bg-desktop@1x.webp");

    @media (max-width: 768px) {
        background-image: url("../img/form-bg-tablet@1x.webp");
    }

    @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
        background-image: url("../img/form-bg-desktop@2x.webp");

        @media (max-width: 768px) {
            background-image: url("../img/form-bg-tablet@1x.webp") !important;
        }
    }

    &::after {
        background-image: url("../img/thanks-bg-desktop@1x.webp");

        @media (max-width: 768px) {
            background-image: url("../img/thanks-bg-tablet@1x.webp");
        }

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url("../img/thanks-bg-desktop@2x.webp");

            @media (max-width: 768px) {
                background-image: url("../img/thanks-bg-tablet@1x.webp") !important;
            }
        }
    }

    &__next-button::before {
        background-image: url("../img/arrow-icon@1x.webp");

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url("../img/arrow-icon@2x.webp");
        }
    }
}

@keyframes shake-input {
    0% {
        transform: translateX(0);
    }

    25% {
        transform: translateX(5%);
    }

    50% {
        transform: translateX(0);
    }

    75% {
        transform: translateX(-5%);
    }

    100% {
        transform: translateX(0);
    }
}

