.our-themes {
    $root: &;

    position: relative;
    padding-top: size-desktop(160px);
    padding-bottom: size-desktop(130px);

    @media (max-width: 540px) {
        padding-top: size-mobile(32px);
        padding-bottom: size-mobile(64px);
    }

    &::before, &::after {
        content: "";
        position: absolute;
        z-index: -1;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: top left;
    }

    &::before {
        top: size-desktop(-316px);
        right: size-desktop(-415px);
        width: size-desktop(970px);
        height: size-desktop(1030px);
        background-image: url("../img/our-themes-wave-0.svg");

        @media (max-width: 540px) {
            top: size-mobile(-100px);
            right: size-mobile(-100px);
            width: size-mobile(350px);
            height: size-mobile(540px);
            background-image: url("../img/our-themes-wave-0-mobile.svg"),
                url("../img/our-themes-wave-0-faded-mobile.svg");
            background-size: size-mobile(350px) auto;
            background-position: top left, top size-mobile(16px) left;
        }
    }

    &::after {
        bottom: size-desktop(135px);
        left: size-desktop(-85px);
        width: size-desktop(509px);
        height: size-desktop(497px);
        transform: rotate(120deg) scaleX(-1);
        background-image: url("../img/our-themes-wave-3.svg");

        @media (max-width: 540px) {
            bottom: size-mobile(175px);
            left: size-mobile(-48px);
            width: size-mobile(340px);
            height: size-mobile(340px);
            transform: rotate(-160deg);
        }
    }

    &__top-description, &__bottom-description, &__subtitle {
        margin: 0;
    }

    &__title, &__top-description {
        max-width: size-desktop(540px);
        margin-left: size-desktop(56px);
        margin-bottom: size-desktop(32px);

        @media (max-width: 540px) {
            max-width: 100%;
            margin-left: size-mobile(22px);
            margin-bottom: size-mobile(16px);
        }
    }

    &__top-description {
        font-size: size-desktop(22px);
        line-height: 1.8em;
        font-weight: 600;

        @media (max-width: 540px) {
            font-size: size-mobile(13px);
            font-weight: 500;
        }
    }

    &__first-wave, &__second-wave {
        box-sizing: border-box;
        background-repeat: no-repeat;

        &::after {
            content: "";
            position: absolute;
            width: size-desktop(109px);
            height: size-desktop(78px);
            background-repeat: no-repeat;
            background-image: url("../img/our-themes-stones.svg");

            @media (max-width: 540px) {
                width: size-mobile(74px);
                height: size-mobile(54px);
            }
        }
    }

    &__first-wave {
        position: absolute;
        top: size-desktop(145px);
        right: size-desktop(175px);
        padding-top: size-desktop(120px);
        padding-left: size-desktop(160px);
        width: size-desktop(645px);
        height: size-desktop(556px);
        background-image: url("../img/our-themes-wave-1.svg");
        background-position: top left;
        background-size: contain;

        @media (max-width: 540px) {
            position: relative;
            top: 0;
            left: 0;
            padding-top: size-mobile(50px);
            padding-left: size-mobile(22px);
            width: size-mobile(290px);
            height: size-mobile(315px);
            background-size: size-mobile(340px) auto;
            background-position: top right;
        }

        #{$root}__list {
            max-width: size-desktop(390px);

            @media (max-width: 540px) {
                max-width: size-mobile(250px);
            }
        }

        &::after {
            right: size-desktop(-124px);
            bottom: size-desktop(136px);
            transform: rotate(72deg);

            @media (max-width: 540px) {
                right: size-mobile(-56px);
                bottom: size-mobile(124px);
                transform: rotate(72deg) scale(0.8);
            }
        }
    }

    &__bottom-wrapper {
        display: flex;
        align-items: flex-end;

        @media (max-width: 540px) {
            flex-direction: column;
            align-items: stretch;
        }
    }

    &__second-wave {
        flex-shrink: 0;
        position: relative;
        z-index: 2;
        margin-left: size-desktop(80px);
        padding-top: size-desktop(110px);
        padding-left: size-desktop(105px);
        width: size-desktop(690px);
        height: size-desktop(636px);
        color: #ffffff;
        background-image: url("../img/our-themes-wave-2.svg"), url("../img/our-themes-wave-2-faded.svg");
        background-size: size-desktop(659px) auto;
        background-position: top left, top size-desktop(24px) left size-desktop(8px);

        @media (max-width: 540px) {
            top: size-mobile(-72px);
            margin-left: size-mobile(16px);
            padding-top: size-mobile(80px);
            padding-left: size-mobile(50px);
            width: 100%;
            height: size-mobile(330px);
            background-image: url("../img/our-themes-wave-2-mobile.svg"),
                url("../img/our-themes-wave-2-mobile-faded.svg");
            background-size: size-mobile(361px) auto;
            background-position: top left, top size-mobile(12px) left size-mobile(8px);
        }

        #{$root}__list {
            @media (max-width: 540px) {
                max-width: size-mobile(270px);
            }
        }

        &::after {
            bottom: size-desktop(120px);
            left: size-desktop(-24px);
            transform: rotate(220deg);

            @media (max-width: 540px) {
                bottom: size-mobile(-48px);
                left: size-mobile(8px);
                transform: rotate(200deg);
            }
        }
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: size-desktop(24px);
        line-height: 1.5em;
        font-weight: 500;
        letter-spacing: 0.03em;

        @media (max-width: 540px) {
            font-size: size-mobile(14px);
            line-height: 1.7em;
        }
    }

    &__item {
        &::before {
            content: "- ";
        }
    }

    &__subtitle {
        max-width: size-desktop(320px);
        margin-bottom: size-desktop(32px);
        font-size: size-desktop(28px);
        line-height: 1.3em;
        font-weight: 700;
        letter-spacing: 0.03em;

        @media (max-width: 540px) {
            max-width: size-mobile(180px);
            margin-bottom: size-mobile(16px);
            font-size: size-mobile(15px);
            letter-spacing: 0.05em;
        }
    }

    &__bottom-description {
        position: relative;
        z-index: 2;
        margin-left: size-desktop(-88px);
        padding-bottom: size-desktop(90px);
        font-size: size-desktop(27px);
        line-height: 1.6em;
        font-weight: 700;
        letter-spacing: -0.05em;

        @media (max-width: 540px) {
            margin-left: 0;
            padding: 0 size-mobile(22px) size-mobile(60px) size-mobile(22px);
            font-size: size-mobile(14px);
            line-height: 1.4em;
            letter-spacing: 0.04em;
        }
    }

    &__top-leaves, &__bottom-leaves {
        position: absolute;
        background-size: contain;
        background-repeat: no-repeat;
    }

    &__top-leaves {
        top: size-desktop(-100px);
        right: size-desktop(-150px);
        width: size-desktop(191px);
        height: size-desktop(458px);
        background-image: url("../img/our-themes-leaves-1@1x.png");

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url("../img/our-themes-leaves-1@2x.png");
        }

        @media (max-width: 540px) {
            top: size-mobile(-16px);
            right: size-mobile(-56px);
            width: size-mobile(74px);
            height: size-mobile(189px);
            background-image: url("../img/our-themes-leaves-1-mobile@1x.png");

            @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
                background-image: url("../img/our-themes-leaves-1-mobile@2x.png");
            }
        }
    }

    &__bottom-leaves {
        top: size-desktop(150px);
        left: size-desktop(-52px);
        width: size-desktop(129px);
        height: size-desktop(330px);
        background-image: url("../img/our-themes-leaves-2@1x.png");

        @media (min-width: 1440px) and (min-resolution: 1.5dppx),
            (min-width: 1440px) and (min-resolution: 144dpi) {
            background-image: url("../img/our-themes-leaves-2@2x.png");
        }

        @media (max-width: 540px) {
            top: auto;
            bottom: size-mobile(-20px);
            left: size-mobile(8px);
            width: size-mobile(83px);
            height: size-mobile(129px);
            background-image: url("../img/our-themes-leaves-2-mobile@1x.png");

            @media (min-width: 1440px) and (min-resolution: 1.5dppx),
                (min-width: 1440px) and (min-resolution: 144dpi) {
                background-image: url("../img/our-themes-leaves-2-mobile@2x.png");
            }
        }
    }
}

html.webp .our-themes {
    &__top-leaves {
        background-image: url("../img/our-themes-leaves-1@1x.webp");

        @media (min-width: 1440px) and (min-resolution: 1.5dppx),
            (min-width: 1440px) and (min-resolution: 144dpi) {
            background-image: url("../img/our-themes-leaves-1@2x.webp");
        }

        @media (max-width: 540px) and (min-resolution: 1.5dppx),
            (max-width: 540px) and (min-resolution: 144dpi) {
            background-image: url("../img/our-themes-leaves-1-mobile@2x.webp");
        }

        @media (max-width: 540px) {
            background-image: url("../img/our-themes-leaves-1-mobile@1x.webp");
        }
    }

    &__bottom-leaves {
        background-image: url("../img/our-themes-leaves-2@1x.webp");

        @media (min-width: 541px) and (min-resolution: 1.5dppx),
            (min-width: 541px) and (min-resolution: 144dpi) {
            background-image: url("../img/our-themes-leaves-2@2x.webp");
        }

        @media (max-width: 540px) and (min-resolution: 1.5dppx),
            (max-width: 540px) and (min-resolution: 144dpi) {
            background-image: url("../img/our-themes-leaves-2-mobile@2x.webp");
        }

        @media (max-width: 540px) {
            background-image: url("../img/our-themes-leaves-2-mobile@1x.webp");
        }
    }
}
