.why-plant-based-food {
    position: relative;
    padding-top: size-desktop(240px);
    padding-bottom: size-desktop(350px);
    padding-left: size-desktop(56px);
    padding-right: size-desktop(56px);
    background-image: url("../img/why-plants-cow@1x.png"),
        url("../img/block-bottom@2x.png"),
        url("../img/why-plants-culture@1x.png"),
        url("../img/why-plants-history.svg"),
        url("../img/why-plants-progress@1x.png");
    background-repeat: no-repeat;
    background-position: bottom size-desktop(200px) left 0,
        bottom size-desktop(80px) right 0,
        top size-desktop(970px) right size-desktop(-200px),
        top size-desktop(115px) left size-desktop(-140px),
        top size-desktop(280px) right size-desktop(-150px);
    background-size: size-desktop(728px) auto,
        100% auto,
        size-desktop(1464px) auto,
        size-desktop(1044px) auto,
        size-desktop(1013px) auto,
        auto;
    overflow: hidden;

    @media (min-width: 541px) and (min-resolution: 1.5dppx),
        (min-width: 541px) and (min-resolution: 144dpi) {
        background-image: url("../img/why-plants-cow@2x.png"),
            url("../img/block-bottom@2x.png"),
            url("../img/why-plants-culture@2x.png"),
            url("../img/why-plants-history.svg"),
            url("../img/why-plants-progress@2x.png");
    }

    @media (max-width: 540px) and (min-resolution: 1.5dppx),
        (max-width: 540px) and (min-resolution: 144dpi) {
        background-image: url("../img/why-plants-top-bg-mobile@2x.png"),
            url("../img/why-plants-cow-mobile@2x.png"),
            url("../img/block-bottom-mobile@2x.png"),
            url("../img/why-plants-progress-mobile.svg"),
            url("../img/why-plants-progress-mobile-bg.svg"),
            url("../img/why-plants-culture-mobile@2x.png"),
            url("../img/why-plants-health-mobile.svg"),
            url("../img/why-plants-history-mobile.svg"),
            url("../img/why-plants-history-mobile-bg.svg"),
            url("../img/why-plants-bottom-bg@2x.png");
    }

    @media (max-width: 540px) {
        padding-top: size-mobile(75px);
        padding-bottom: size-mobile(110px);
        padding-left: size-mobile(20px);
        padding-right: size-mobile(20px);
        background-image: url("../img/why-plants-top-bg-mobile@1x.png"),
            url("../img/why-plants-cow-mobile@1x.png"),
            url("../img/block-bottom-mobile@2x.png"),
            url("../img/why-plants-progress-mobile.svg"),
            url("../img/why-plants-progress-mobile-bg.svg"),
            url("../img/why-plants-culture-mobile@1x.png"),
            url("../img/why-plants-health-mobile.svg"),
            url("../img/why-plants-history-mobile.svg"),
            url("../img/why-plants-history-mobile-bg.svg"),
            url("../img/why-plants-bottom-bg@1x.png");
        background-position: top size-mobile(-71px) left size-mobile(8px),
            top size-mobile(950px) left size-mobile(160px),
            bottom size-mobile(50px) left 0,
            top size-mobile(510px) right size-mobile(-60px),
            top size-mobile(530px) right size-mobile(-60px),
            top size-mobile(1155px) right size-mobile(-100px),
            top size-mobile(800px) left size-mobile(-50px),
            top size-mobile(95px) left size-mobile(-160px),
            top size-mobile(95px) left size-mobile(-140px),
            bottom size-mobile(-70px) right size-mobile(-100px);
        background-size: 95% auto,
            size-mobile(310px) auto,
            100% auto,
            size-mobile(400px) auto,
            size-mobile(400px) auto,
            size-mobile(440px) auto,
            size-mobile(377px) auto,
            size-mobile(535px) auto,
            size-mobile(535px) auto,
            size-mobile(239px) auto,
            auto;
        z-index: 0;
    }

    &__circle {
        position: absolute;
        top: size-desktop(970px);
        right: size-desktop(100px);
        width: size-desktop(206px);
        height: size-desktop(206px);
        background-image: url("../img/why-plants-circle.svg");
        background-size: contain;

        @media (max-width: 540px) {
            top: 0;
            right: size-mobile(70px);
            width: size-mobile(56px);
            height: size-mobile(56px);
            background-image: none;
            background-color: #377c53;
            border-radius: 50%;
            transform: none !important;

            &:before {
                content: "";
                position: absolute;
                z-index: -1;
                top: size-mobile(10px);
                right: size-mobile(-10px);
                width: size-mobile(56px);
                height: size-mobile(56px);
                background-color: rgba(0, 0, 0, 0.1);
                border-radius: 50%;
            }
        }
    }

    &__leaf {
        position: absolute;
        width: size-desktop(405px);
        height: size-desktop(385px);
        background-image: url("../img/why-plants-leaf.svg");
        background-repeat: no-repeat;
        background-size: contain;
        transform: scale(-1, 1) rotate(-14deg);

        &--top {
            top: size-desktop(120px);
            right: size-desktop(40px);

            @media (max-width: 540px) {
                z-index: -1;
            }

            @media (max-width: 540px) {
                top: size-mobile(110px);
                right: size-mobile(20px);
                width: size-mobile(180px);
                height: size-mobile(250px);
            }
        }

        &--bottom {
            bottom: size-desktop(150px);
            left: size-desktop(-110px);

            @media (max-width: 540px) {
                display: none;
            }
        }
    }

    &__title {
        margin-bottom: size-desktop(60px);
        width: size-desktop(600px);

        @media (max-width: 540px) {
            margin-bottom: size-mobile(20px);
            width: size-mobile(250px);

            &::before {
                bottom: size-mobile(110px);
            }
        }
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    &__item {
        line-height: size-desktop(40px);
        font-weight: 500;

        @media (max-width: 540px) {
            line-height: 1.6em;
            font-size: size-mobile(14px);
        }

        &--history {
            width: size-desktop(750px);
            font-size: size-desktop(22px);
            font-weight: 600;

            @media (max-width: 540px) {
                margin-bottom: size-mobile(100px);
                width: size-mobile(310px);
                line-height: 1.7em;
                font-size: size-mobile(13px);
            }
        }

        &--progress {
            margin-left: auto;
            margin-top: size-desktop(-190px);
            width: size-desktop(520px);
            font-size: size-desktop(23px);
            text-align: right;

            @media (max-width: 540px) {
                margin-top: 0;
                margin-bottom: size-mobile(50px);
                width: size-mobile(270px);
            }
        }

        &--health {
            margin-bottom: size-desktop(72px);
            width: size-desktop(700px);
            font-size: size-desktop(24px);

            @media (max-width: 540px) {
                margin-bottom: size-mobile(95px);
                width: size-mobile(230px);
                line-height: 1.7em;
            }
        }

        &--culture {
            margin-bottom: size-desktop(100px);
            margin-left: auto;
            width: size-desktop(585px);
            font-size: size-desktop(23px);
            text-align: right;

            @media (max-width: 540px) {
                margin-bottom: size-mobile(88px);
                width: size-mobile(268px);
                font-size: size-mobile(15px);
            }
        }

        &--answer {
            margin-left: auto;
            width: size-desktop(850px);
            font-size: size-desktop(30px);
            font-weight: 600;
            text-align: right;

            @media (max-width: 540px) {
                text-align: justify;
                line-height: 1.4em;
                font-size: size-mobile(15px);
                width: 100%;
            }
        }
    }
}

html.webp .why-plant-based-food {
    background-image: url("../img/why-plants-cow@1x.webp"),
        url("../img/block-bottom@2x.webp"),
        url("../img/why-plants-culture@1x.webp"),
        url("../img/why-plants-history.svg"),
        url("../img/why-plants-progress@1x.webp");

    @media (min-width: 541px) and (min-resolution: 1.5dppx),
        (min-width: 541px) and (min-resolution: 144dpi) {
        background-image: url("../img/why-plants-cow@2x.webp"),
            url("../img/block-bottom@2x.webp"),
            url("../img/why-plants-culture@2x.webp"),
            url("../img/why-plants-history.svg"),
            url("../img/why-plants-progress@2x.webp");
    }

    @media (max-width: 540px) and (min-resolution: 1.5dppx),
        (max-width: 540px) and (min-resolution: 144dpi) {
        background-image: url("../img/why-plants-top-bg-mobile@2x.webp"),
            url("../img/why-plants-cow-mobile@2x.webp"),
            url("../img/block-bottom-mobile@2x.webp"),
            url("../img/why-plants-progress-mobile.svg"),
            url("../img/why-plants-progress-mobile-bg.svg"),
            url("../img/why-plants-culture-mobile@2x.webp"),
            url("../img/why-plants-health-mobile.svg"),
            url("../img/why-plants-history-mobile.svg"),
            url("../img/why-plants-history-mobile-bg.svg"),
            url("../img/why-plants-bottom-bg@2x.webp");
    }

    @media (max-width: 540px) {
        padding-top: size-mobile(75px);
        padding-bottom: size-mobile(110px);
        padding-left: size-mobile(20px);
        padding-right: size-mobile(20px);
        background-image: url("../img/why-plants-top-bg-mobile@1x.webp"),
            url("../img/why-plants-cow-mobile@1x.webp"),
            url("../img/block-bottom-mobile@2x.webp"),
            url("../img/why-plants-progress-mobile.svg"),
            url("../img/why-plants-progress-mobile-bg.svg"),
            url("../img/why-plants-culture-mobile@1x.webp"),
            url("../img/why-plants-health-mobile.svg"),
            url("../img/why-plants-history-mobile.svg"),
            url("../img/why-plants-history-mobile-bg.svg"),
            url("../img/why-plants-bottom-bg@1x.webp");
        z-index: 0;
    }
}
