.our-product {
    $root: &;

    position: relative;
    padding-top: size-desktop(230px);
    padding-bottom: size-desktop(90px);
    color: #ffffff;
    background-color: #6c1b6a;
    background-image: url('../img/our-product-dots.svg'), url('../img/our-product-bg-wave.png');
    background-repeat: no-repeat;
    background-size: size-desktop(979px) auto, 100% auto;
    background-position: bottom size-desktop(780px) left, top size-desktop(1072px) left;

    @media (max-width: 540px) {
        padding-top: size-mobile(28px);
        padding-bottom: size-mobile(36px);
        background-image: url('../img/our-product-dots.svg'),
            url('../img/our-product-bg-wave-mobile.png');
        background-size: 100% auto;
        background-position: bottom size-mobile(336px) left, bottom size-mobile(24px) left;
    }

    &::before,
    &::after {
        content: '';
        position: absolute;
        background-repeat: no-repeat;
    }

    &::before {
        top: size-desktop(-130px);
        left: 0;
        width: 100%;
        height: size-desktop(440px);
        background-color: #6c1b6a;
        background-image: url('../img/block-bottom@2x.png'), url('../img/our-product-wave-0.svg'),
            url('../img/our-product-bg-top.svg');
        background-position: top size-desktop(120px) left, bottom left 40%,
            bottom size-desktop(164px) center;
        background-size: 100% auto, size-desktop(388px) auto, 112% auto;

        @media (max-width: 540px) {
            top: size-mobile(-156px);
            height: size-mobile(215px);
            background-image: url('../img/block-bottom-mobile@2x.png'),
                url('../img/our-product-wave-0.svg'), url('../img/our-product-bg-top.svg');
            background-position: top size-mobile(80px) left, bottom right size-mobile(16px),
                bottom size-mobile(56px) center;
            background-size: 100% auto, auto size-mobile(160px), 124% auto;
        }
    }

    &::after {
        z-index: 1;
        right: 0;
        bottom: size-desktop(-60px);
        width: size-desktop(376px);
        height: size-desktop(648px);
        transform: scaleX(-1);
        background-image: url('../img/our-ideas-plant-0.svg');
        background-size: contain;

        @media (max-width: 540px) {
            bottom: size-mobile(-96px);
            width: size-mobile(134px);
            height: size-mobile(266px);
        }
    }

    &__title {
        margin-bottom: size-desktop(20px);
        margin-left: size-desktop(56px);
        max-width: size-desktop(600px);

        @media (max-width: 540px) {
            margin-left: size-mobile(22px);
            margin-bottom: size-mobile(6px);
            max-width: size-mobile(240px);
        }
    }

    &__waves-wrapper {
        position: relative;
    }

    &__first-wave,
    &__second-wave {
        box-sizing: border-box;
        color: #000000;

        &::before {
            content: '';
            position: absolute;
            background-repeat: no-repeat;
        }
    }

    &__first-wave {
        padding-top: size-desktop(140px);
        padding-left: size-desktop(56px);
        width: size-desktop(760px);
        height: size-desktop(685px);
        font-size: size-desktop(24px);
        line-height: 1.5em;
        font-weight: 500;
        letter-spacing: 0.02em;

        @media (max-width: 540px) {
            padding-top: size-mobile(64px);
            padding-left: size-mobile(22px);
            width: 100%;
            height: size-mobile(369px);
            font-size: size-mobile(14px);
            letter-spacing: 0;
        }

        &::before {
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-image: url('../img/our-product-wave-1.svg'),
                url('../img/our-product-wave-1-faded.svg');
            background-size: size-desktop(760px) auto;
            background-position: left size-desktop(-40px) top, left top size-desktop(16px);

            @media (max-width: 540px) {
                background-image: url('../img/our-product-wave-1-mobile.svg');
                background-size: size-mobile(407px) auto;
                background-position: top right;
            }
        }
    }

    &__second-wave {
        position: absolute;
        top: size-desktop(64px);
        right: 0;
        padding-top: size-desktop(205px);
        padding-right: size-desktop(102px);
        text-align: right;
        font-size: size-desktop(30px);
        line-height: 1.6em;
        font-weight: 600;
        letter-spacing: 0.02em;

        @media (max-width: 540px) {
            position: relative;
            top: size-mobile(-50px);
            padding: size-mobile(96px) size-mobile(22px) size-mobile(80px);
            font-size: size-mobile(14px);
            font-weight: 500;
            letter-spacing: 0;
        }

        &::before {
            z-index: 2;
            right: 0;
            bottom: size-desktop(-200px);
            width: size-desktop(815px);
            height: size-desktop(915px);
            background-image: url('../img/our-product-wave-2.svg'),
                url('../img/our-product-wave-2-faded.svg');
            background-size: size-desktop(860px) auto;
            background-position: top left, top size-desktop(24px) left size-desktop(16px);

            @media (max-width: 540px) {
                top: 0;
                bottom: auto;
                width: 100%;
                height: 100%;
                background-image: url('../img/our-product-wave-2-mobile.svg'),
                    url('../img/our-product-wave-2-mobile-faded.svg');
                background-size: size-mobile(402px) auto;
                background-position: top size-mobile(16px) right size-mobile(-48px),
                    top size-mobile(28px) right size-mobile(-64px);
            }
        }

        &::after {
            content: '';
            position: absolute;
            z-index: 2;
            top: size-desktop(-326px);
            right: 0;
            width: size-desktop(630px);
            height: size-desktop(676px);
            background-image: url('../img/our-product-figure-1@1x.png');
            background-size: cover;
            background-repeat: no-repeat;

            @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
                background-image: url('../img/our-product-figure-1@2x.png');
            }

            @media (max-width: 540px) {
                top: size-mobile(-119px);
                right: size-mobile(-40px);
                width: size-mobile(246px);
                height: size-mobile(286px);
            }
        }
    }

    &__first-thesis-paragraph,
    &__second-thesis {
        position: relative;
        z-index: 3;
        margin: 0;
    }

    &__first-thesis-paragraph {
        max-width: size-desktop(530px);
        margin-bottom: size-desktop(24px);

        @media (max-width: 540px) {
            max-width: size-mobile(300px);
        }

        &:last-child {
            margin-bottom: 0;

            @media (max-width: 540px) {
                max-width: size-mobile(200px);
            }
        }
    }

    &__second-thesis {
        max-width: size-desktop(620px);

        @media (max-width: 540px) {
            max-width: 100%;
        }
    }

    &__second-thesis-bottom {
        @media (max-width: 540px) {
            display: inline-block;
            max-width: size-mobile(240px);
        }
    }

    &__goods-wrapper {
        padding-top: size-desktop(340px);

        @media (max-width: 540px) {
            padding-top: size-mobile(56px);
        }
    }

    &__goods-list {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0;
        padding: 0;
        list-style: none;
        color: #000000;

        @media (max-width: 540px) {
            align-items: stretch;
        }
    }

    &__good-wrapper {
        position: relative;
        margin-bottom: size-desktop(330px);

        @media (max-width: 540px) {
            margin-bottom: size-mobile(188px);
        }

        #{$root}__good-name-wrapper {
            @media (max-width: 540px) {
                display: flex;
                flex-direction: column;
                align-items: flex-end;
            }
        }

        #{$root}__good-preview {
            height: auto;
        }

        &--cutlets,
        &--hiburger {
            padding-left: size-desktop(250px);
            left: size-desktop(-32px);

            @media (max-width: 540px) {
                left: 0;
                padding-left: 0;
                padding-right: size-mobile(16px);
            }
        }

        &--cutlets {
            $indent: size-desktop(140px);

            #{$root}__good-image-wrapper {
                top: 100%;
                right: calc(100% - #{$indent});
                transform: translateY(-60%);

                @media (max-width: 540px) {
                    top: 50%;
                    right: auto;
                    left: 0;
                    transform: translateY(-50%);
                }

                &::before {
                    top: size-desktop(-16px);
                    left: 52%;
                    width: size-desktop(111px);
                    height: size-desktop(102px);
                    background-image: url('../img/our-product-non-gmo.svg');
                    animation: rotate-1 4.2s alternate ease-in-out infinite;

                    @media (max-width: 540px) {
                        top: size-mobile(24px);
                        width: size-mobile(46px);
                        height: size-mobile(42px);
                    }
                }

                &::after {
                    top: size-desktop(40px);
                    right: size-desktop(-32px);
                    width: size-desktop(153px);
                    height: size-desktop(136px);
                    background-image: url('../img/our-product-vegan.svg');
                    animation: rotate-2 2.2s alternate-reverse linear infinite;

                    @media (max-width: 540px) {
                        top: size-mobile(56px);
                        right: size-mobile(-8px);
                        width: size-mobile(64px);
                        height: size-mobile(57px);
                    }
                }
            }

            #{$root}__good-preview {
                width: size-desktop(480px);

                @media (max-width: 540px) {
                    width: size-mobile(198px);
                }
            }
        }

        &--hiburger {
            $indent: size-desktop(180px);

            #{$root}__good-image-wrapper {
                top: 50%;
                right: calc(100% - #{$indent});
                transform: translateY(-50%);

                @media (max-width: 540px) {
                    right: auto;
                    left: 0;
                }

                &::before {
                    top: size-desktop(40px);
                    right: 0;
                    width: size-desktop(147px);
                    height: size-desktop(129px);
                    background-image: url('../img/our-product-omega.svg');
                    animation: rotate-1 3.2s alternate linear infinite;

                    @media (max-width: 540px) {
                        top: auto;
                        right: auto;
                        bottom: size-mobile(24px);
                        left: size-mobile(8px);
                        width: size-mobile(61px);
                        height: size-mobile(54px);
                    }
                }

                &::after {
                    bottom: size-desktop(80px);
                    left: size-desktop(-16px);
                    width: size-desktop(183px);
                    height: size-desktop(162px);
                    background-image: url('../img/our-product-fiber.svg');
                    animation: rotate-2 2.2s alternate linear infinite;

                    @media (max-width: 540px) {
                        top: size-mobile(8px);
                        right: size-mobile(6px);
                        bottom: auto;
                        left: auto;
                        width: size-mobile(76px);
                        height: size-mobile(67px);
                    }
                }
            }

            #{$root}__good-preview {
                width: size-desktop(495px);

                @media (max-width: 540px) {
                    width: size-mobile(208px);
                }
            }
        }

        &--higgets {
            $indent: size-desktop(210px);
            padding-right: size-desktop(250px);

            @media (max-width: 540px) {
                padding-right: 0;
                padding-left: size-mobile(16px);
            }

            #{$root}__good-name-wrapper {
                @media (max-width: 540px) {
                    align-items: flex-start;
                }
            }

            #{$root}__good-image-wrapper {
                top: 50%;
                left: calc(100% - #{$indent});
                transform: translateY(-50%);

                @media (max-width: 540px) {
                    right: 0;
                    left: auto;
                    transform: translateY(-50%);
                }

                &::before {
                    top: size-desktop(40px);
                    right: size-desktop(80px);
                    width: size-desktop(175px);
                    height: size-desktop(156px);
                    background-image: url('../img/our-product-b12.svg');
                    animation: rotate-1 4.2s alternate linear infinite;

                    @media (max-width: 540px) {
                        top: 0;
                        right: size-mobile(8px);
                        width: size-mobile(73px);
                        height: size-mobile(69px);
                    }
                }
            }

            #{$root}__good-preview {
                width: size-desktop(562px);

                @media (max-width: 540px) {
                    width: size-mobile(224px);
                }
            }
        }
    }

    &__good-name-wrapper {
        display: grid;
        margin: 0;
    }

    &__good-type {
        font-size: size-desktop(44px);
        line-height: 1em;
        text-transform: uppercase;
        font-weight: 800;
        letter-spacing: 0.04em;

        @media (max-width: 540px) {
            font-size: size-mobile(19px);
        }
    }

    &__good-addition {
        font-size: size-desktop(38px);
        line-height: 1.1em;
        font-weight: 600;
        text-transform: lowercase;

        @media (max-width: 540px) {
            font-size: size-mobile(16px);
        }
    }

    &__good-image-wrapper {
        position: absolute;

        &::before,
        &::after {
            content: '';
            position: absolute;
            background-repeat: no-repeat;
            background-size: contain;
        }
    }
}

html.webp .our-product {
    background-image: url('../img/our-product-dots.svg'), url('../img/our-product-bg-wave.webp');

    @media (max-width: 540px) {
        background-image: url('../img/our-product-dots.svg'),
            url('../img/our-product-bg-wave-mobile.webp');
    }

    &__second-wave::after {
        background-image: url('../img/our-product-figure-1@1x.webp');

        @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
            background-image: url('../img/our-product-figure-1@2x.webp');
        }
    }
}

@keyframes rotate-1 {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(10deg);
    }
}

@keyframes rotate-2 {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(4deg);
    }
}
